<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
      </div>

      <div class="vs-row flex hover mb-base">
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('ID') }}:</span>
            <span>{{ communicationEvent.communicationEventId }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('COMMUNICATION_TYPE') }}:</span>
            <span>{{ communicationEvent.communicationEventTypeDescription }}</span>
          </p>
          <p class="info-row" v-if="communicationEvent.contactMechTypeDescription">
            <span>{{ $t('CONTACT_TYPE') }}:</span>
            <span>{{ communicationEvent.contactMechTypeDescription }}</span>
          </p>
          <p class="info-row" v-if="communicationEvent.statusDescription">
            <span>{{ $t('STATUS') }}:</span>
            <span>{{ communicationEvent.statusDescription }}</span>
          </p>
          <p class="info-row" v-if="communicationEvent.reasonDescription">
            <span>{{ $t('REASON') }}:</span>
            <span>{{ communicationEvent.reasonDescription }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ENTRY_DATE') }}:</span>
            <span>{{ communicationEvent.entryDate | formatUnixDate }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row" v-if="communicationEvent.fromPartyId">
            <span>{{ $t('FROM_PARTY') }}:</span>
            <span><router-link :to="{ name: 'ViewParty', params: { id: communicationEvent.fromPartyId } }">{{ communicationEvent.fromPartyName }}</router-link></span>
          </p>
          <p class="info-row" v-if="communicationEvent.toPartyId">
            <span>{{ $t('TO_PARTY') }}:</span>
            <span><router-link :to="{ name: 'ViewParty', params: { id: communicationEvent.toPartyId } }">{{ communicationEvent.toPartyName }}</router-link></span>
          </p>
          <p class="info-row" v-if="communicationEvent.datetimeStarted">
            <span>{{ $t('START_DATE') }}:</span>
            <span>{{ communicationEvent.datetimeStarted | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="communicationEvent.datetimeEnded">
            <span>{{ $t('END_DATE') }}:</span>
            <span>{{ communicationEvent.datetimeEnded | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="communicationEvent.subject">
            <span>{{ $t('SUBJECT') }}:</span>
            <span>{{ communicationEvent.subject }}</span>
          </p>
        </div>
      </div>

      <vs-collapse type="border" class="mb-base" v-if="communicationEvent.body || communicationEvent.note">
        <vs-collapse-item v-if="communicationEvent.body">
          <div slot="header">
            {{ $t('BODY') }}
          </div>
          <div class="text-italic">
            {{ communicationEvent.body }}
          </div>
        </vs-collapse-item>
        <vs-collapse-item v-if="communicationEvent.note">
          <div slot="header">
            {{ $t('NOTES') }}
          </div>
          <div class="text-italic">
            {{ communicationEvent.note }}
          </div>
        </vs-collapse-item>
      </vs-collapse>

      <vs-tabs>
        <vs-tab :label="$t('CONTENT')">
          <communication-event-content @show-alert="showAlert" :communicationEventId="communicationEventId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CommunicationEventService from '@/services/nucleus/CommunicationEventService'
import CommunicationEventContent from '@/views/nucleus/communication-event/communication-event-view/CommunicationEventContent'

export default {
  name: 'communication-event-view',
  mixins: [AlertMixin],
  components: {
    CommunicationEventContent
  },
  data: function () {
    return {
      communicationEventId: this.$route.params.id,
      communicationEvent: {}
    }
  },
  methods: {
    async load () {
      let self = this
      CommunicationEventService.communicationEvents({
        communicationEventId: this.communicationEventId
      }).then(function (response) {
        self.communicationEvent = response.data.eventList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyCommunicationEvent', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
