<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="contentFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FILE') }}</label>
          <nv-input-group class="bg-white">
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-folder"></i>
              </div>
            </template>
            <input class="vs-inputx vs-input--input normal" type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('CONTENT_TYPE') }}</label>
          <vs-select autocomplete class="nv-col sm:w w-full" v-model="contentTypeEnumId">
            <vs-select-item class="w-full" v-for="(enumeration, index) in contentTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
          <vs-textarea class="w-full" v-model="contentDescription"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createEventContent" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideContentForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="contentTableData" :noDataText="contentTableNoDataText" :sst="true" search @search="filterContentTable" @sort="orderContentTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showContentForm" :disabled="contentFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshContentTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="contentTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ contentTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="contentTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="contentTypeDescription">
          {{ this.$t('CONTENT_TYPE') }}
        </vs-th>
        <vs-th sort-key="description">
          {{ this.$t('DESCRIPTION') }}
        </vs-th>
        <vs-th sort-key="inputUserFullName">
          {{ this.$t('INPUT_BY') }}
        </vs-th>
        <vs-th sort-key="contentDate">
          {{ this.$t('CREATION_DATE') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].contentTypeDescription">
            <span>{{ data[index].contentTypeDescription }}</span>
            <a :href="contentDownloadUrl(data[index])" class="ml-2"><i class="far fa-cloud-download-alt text-primary"></i></a>
          </vs-td>
          <vs-td :data="data[index].description">
            {{ data[index].description | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].inputUserFullName">
            {{ data[index].inputUserFullName }}
          </vs-td>
          <vs-td :data="data[index].contentDate">
            {{ data[index].contentDate | formatUnixDate }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="contentTableTotalPages" v-model="contentTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import CommunicationEventService from '@/services/nucleus/CommunicationEventService'

export default {
  name: 'communication-event-content',
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    communicationEventId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      contentTableData: [],
      contentTableFilter: null,
      contentTableFilterTimer: null,
      contentTableOrderBy: null,
      contentTableContainer: 'loading-container',
      contentTableBusy: false,
      contentTableTotalRows: 0,
      contentTableCurrentPage: 1,
      contentTablePerPage: 10,
      contentFormVisible: false,
      contentDescription: '',
      contentFile: null,
      contentTypes: [],
      contentTypeEnumId: 'CmctAttachment'
    }
  },
  computed: {
    contentTableNoDataText: function () {
      return this.contentTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    contentTableTotalPages: function () {
      return Math.ceil(this.contentTableTotalRows / this.contentTablePerPage)
    },
    contentTablePagingInfo: function () {
      return this.pagingInfo(this.contentTableCurrentPage, this.contentTablePerPage, this.contentTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'CommunicationContentType',
        orderByField: 'sequenceNum,description',
        pageNoLimit: true
      }).then(function (response) {
        self.contentTypes = response.data.enumerationList || []
      })
    },
    loadContentTableData (loadingContainerId) {
      let self = this
      this.contentTableBusy = true
      this.contentTableContainer = loadingContainerId || this.contentTableContainer
      this.startLoading(this.contentTableContainer)
      return CommunicationEventService.communicationEventContents({
        pageIndex: this.contentTableCurrentPage - 1,
        pageSize: this.contentTablePerPage,
        filter: this.contentTableFilter,
        orderByField: this.contentTableOrderBy,
        communicationEventId: this.communicationEventId
      }).then(function (response) {
        self.stopLoading(self.contentTableContainer)
        self.contentTableBusy = false
        self.contentTableTotalRows = response.data.totalRows
        self.contentTableData = response.data.contentList || []
      })
    },
    refreshContentTable () {
      this.loadContentTableData()
    },
    filterContentTable (filter) {
      let self = this
      if (this.contentTableFilter !== filter) {
        clearTimeout(this.contentTableFilterTimer)
        this.contentTableFilterTimer = setTimeout(() => {
          self.contentTableFilter = filter
          self.loadContentTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.contentTableFilterTimer)
      }
    },
    orderContentTable (orderBy, active) {
      this.contentTableOrderBy = (active ? '' : '-') + orderBy
      this.loadContentTableData()
    },
    handleFileUpload () {
      this.contentFile = this.$refs.file.files[0]
    },
    contentDownloadUrl: function (item) {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      return `${baseUrl}/apps/Banqio/DownloadSecure/downloadCommunicationEventContent?sourceAppId=${appId}&communicationEventId=${item.communicationEventId}&communicationEventContentId=${item.communicationEventContentId}`
    },
    showContentForm () {
      this.contentFormVisible = true
    },
    hideContentForm () {
      this.contentFormVisible = false
    },
    createEventContent () {
      let self = this
      let formData = new FormData()
      formData.append('communicationEventId', this.$route.params.id)
      formData.append('contentTypeEnumId', this.contentTypeEnumId)
      formData.append('contentFile', this.contentFile)
      formData.append('description', this.contentDescription)
      CommunicationEventService.createCommunicationEventContent({
        communicationEventId: this.$route.params.id,
        formData: formData
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshContentTable()
          self.hideContentForm()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  watch: {
    contentTableCurrentPage: function () {
      this.loadContentTableData()
    },
    contentTablePerPage: function () {
      this.loadContentTableData()
    }
  },
  mounted () {
    this.load()
    this.loadContentTableData()
  }
}
</script>
